// Color Varables
$border-color: #e6e6e6;
$primary-color: #5cb85c;
$transparent-black: rgba(0, 0, 0, 0.5);
$body-background: rgb(240,242,245);
$box-shadow: rgba(168, 155, 155, 0.1);
$my-text-color: #666562;
// Universel Selector
body{
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    background-color: $body-background;
}

// Navigation Start
nav{
    border-bottom: 1px solid $border-color;
    padding: 1% 0;
    display: flex;
    height: 50px;
    background-color: white;
    box-shadow: 0px 5px $box-shadow;
}
.container{
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}
.logo{
    width: 10%;
    background: url(../images/nav-logo.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 0%;
}
.res-menu{
    width: 90%;
    display: flex;
    justify-content: space-between;
}
.menu-items{
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}
.menu-items ul{
    list-style: none;
    width: 60%;
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
}
.menu-items ul li a{
    text-decoration: none;
    color: $my-text-color;
}
.search{
    width: 25%;
    height: 70%;
    display: flex;
    align-self: center;
    align-items: center;
    padding: 0 2%;
    border: 0.5px solid $border-color;
    border-radius: 50px;
}
.search input{
    border: none !important;
    border-color: transparent !important;
    width: 80%;
}
.search label i{
    cursor: pointer;
}
.search input:focus{
    outline: none;
}
.nav-buttons{
    width: 20%;
    display: flex;
    justify-content: space-around;
    align-self: center;
}

.signup, .login{
    color: $my-text-color;
    padding: 3.5% 9%;
    
}
.signup{
    background-color: $primary-color;
    color: white;
    border: 0.5px solid $border-color;
    border-radius: 10px;
}
.nav-toggle{
    display: none;
}
.nav-toggle-2nd{
    display: none;
}
// Media Query
@media only screen and(max-width:960px){
    .container{
        width:96%;
    }
    .signup, .login{    
        padding: 3% 8%;
    }
    .menu-items{
        display: block;
        width: 60vw;
        color: $primary-color;
        position: absolute;
        left: -100%;
        top: 69px;
        // background: $transparent-black;
        transition: all 0.5s;
        border-right: 0.5px solid $border-color;
        border-bottom: 0.5px solid $border-color;
    }
    .show{
        left: 0%;
    }
    .nav-toggle-first{
        display: none;
    }
    .nav-toggle-2nd{
        display: block;
    }
    .menu-items ul{
        width: 100%;
        text-transform: uppercase;
        color: inherit;
        text-align: left;
        line-height: 30px;
        display: block;
        margin-left: 12%;
        text-align: left;    
    }
    .search{
        width: 90%;
        height: 10vh;
        margin-left: 2.5%;
        margin-bottom: 5vh;  
    }
    .search form{
        width: 100%;
    }
    .search form label{
        width: 25%;
    }
    .search form input{
        width: 95%;
    }
    .menu-items ul li{
        margin-left: -15%;
    }
    .logo{
        width: 20%;
    }
    .res-menu{
        width: 70%;
    }
    .nav-buttons{
        width: 50%;
        margin-left: auto;
    }

    .nav-toggle{
        width: 4%;
        display: block;
        cursor: pointer;
        font-size: 25px;
        margin-right: 30px;
        margin-left: 10px;
        margin-top: .3%;
        align-self: center;
        color: $primary-color;
    }
    
    

}
// Tab

@media only screen and(max-width:880px){
    .container{
        width:98%;
    }
    .menu-items{
        top: 66px;
    }
    .search form input{
        width: 90%;
        background: transparent;
    }
}
// Mobile
@media only screen and(max-width:680px){
    .menu-items{
        top: 62px;
    }
    .search{
        height: 40px;  
    }
    .search form input{
        width: 90%;
    }
}
@media only screen and(max-width:480px){
    .container{
        width:95%;
    }
    .menu-items{
        top: 60px;
    }
    .nav-toggle{ 
        font-size: 20px;
    }
    .signup, .login{
    
        padding: 4% 9%;
        
    }
    .logo{
        width: 30%;
    }
    .res-menu{
        width: 70%;
    }
    .nav-buttons{
        width: 70%;
        margin-left: auto;
    }

    .search form input{
        width: 80%;
    }
    .search{
        height: 8vh;
    }
}
// Small Scree less 392
// Mobile
@media only screen and(max-width:392px){

    .logo{
        width: 30%;
    }
    .menu-items{
        top: 55px;
    }
    .res-menu{
        width: 70%;
    }
    .nav-buttons{
        width: 80%;
        margin-left: auto;
    }
    
    .nav-toggle{
        width: 4%;
    }
}
@media only screen and(max-width:330px){
    .nav-buttons{
        width: 90%;
    }
}
// ---------------------------Div Ends-----------------


// --------------------------- Mian Section Start-----------------
.main-section{
    width: 100%;
    display: flex;
    justify-content: space-around;
}
.top-rated{
    width: 28%;
}
.top-questions{
    width: 40%;
    // background-color: rebeccapurple;
}
.suggest-problems{
    width: 28%;
    // background-color: red;
    // background-color: #5cb85c;
}
// --------------------------- Border Part of Each Part -----------------
.my-section-border{
    border: 1px solid $border-color;
    background-color: white;
    border-radius: 10px;
}
// --------------------------- Post Question Start -----------------
.post-question{
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-left: auto;
    margin-top: 35px;
    margin-bottom: 12px;
    padding: 5px 5px;
}

.profile-question{
    width: 50px;
    height: 50px;
}
.question-profile{
    width: 100%;
    height: 100%;
    border: 1px solid $border-color;
    border-radius: 50%;
}
.post-question-content{
    width: 78%;
    height: 100%;
    background-color: $body-background;
    align-self: center;
    padding: 0px 2%;
    border: 0.5px solid $border-color;
    border-radius: 50px;
}
.post-question-content input{
    border: none !important;
    border-color: transparent !important;
    background-color: $body-background;
    width: 100%;
    outline: none; 
    padding: 10px 0;
}

// --------------------------- Post Question Ends -----------------

// --------------------------- Top Question Starts -----------------
.question{
    width: 100%;
    margin: 0 auto;
    padding: 5px 5px;
}
.qu-person-detail{
    margin-top: 10px;
    // border-top: 1px solid $border-color;
    width: 95%;
    margin: 0 auto;
    display: flex;
}
.que-profile{
    width: 50px;
    height: 50px;
    margin-top: 6px;
}
.que-profile img{
    width: 100%;
    height: 100%;
    border: 1px solid $border-color;
    border-radius: 50%; 
}
.qu-name{
    width: 78%;
    padding-top: 10px;
    margin: 15px 15px;
    color: $my-text-color;
}
.qu-name a,span{
    text-decoration: none;
    color: $my-text-color;    
}
.question-content{
    margin: 15px auto;
    text-align: justify;
    width: 95%;
    color: $my-text-color;
}


.respond{
    width: 95%;
    margin: 15px auto;
    border-top: 1px solid $border-color;
    border-bottom: 1px solid $border-color;
    padding: 10px 0;
    display: flex;
    justify-content: space-around;
    color: inherit;
}
.respond span{
    transition: transform 0.6s;
}
.respond span:hover{
    transform: scale(1.5); 
}
// --------------------------------- Uploaded Answer -----------------------------
.uploaded-answer{
    border: 1px solid $border-color;
    border-radius: 10px;
    padding: 5px 10px;
    width: 92%;
    margin: 0 auto;
    color: $my-text-color;  
}
.uploaded-answer a{
    text-decoration: none;
    color: $my-text-color;
}
.answer{
    cursor: pointer;
}
#panel{
    display: none;
}
.answer-text{
    margin-top: 2px;
    text-align: justify;
    margin-bottom: 5px;
}
// --------------------------------Write Answer------------------------------------
.write-answer{
    width: 95%;
    margin: 0 auto;
    margin-top: 15px;
    // height: 30px;
    border: 1px solid $border-color;
    border-radius: 10px;
}
.write-answer form{
    display: flex;
    justify-content: space-evenly;
}
.write-answer form input{
    width: 90%;
    outline: none;
    // height: 100%;
    padding: 13px 0;
    // color: white;
    border: none !important;
    border-color: transparent !important;

}
.answer-submit{
    align-self: center;
    font-size: 25px;
    color: $my-text-color;
}
// -----------------------Right Suggestions-----------------------------

.suggested-container{
    box-sizing: border-box; 
    width: 100%;
    padding: 0 5px;
    margin-top: 35px;
}
.suggested-heading{
    text-transform: uppercase;
    color: $my-text-color;
    padding: 19px 10px;
    width: 100%;
    box-sizing: border-box;
    // margin: 0 10px;
    color: $my-text-color;
}
.main-problem{
    width: 100%;
    margin-top: 5px;
    border-top: 1px solid $border-color;
    box-sizing: border-box;
    text-align: justify;
    cursor: pointer;
    padding: 10px;
    transition: transform 0.6s;
}
.main-problem:hover{
    transform: scale(1.05);
}
.main-problem p{
    margin-top: 10px;
    color: $my-text-color;
}
.qu-information{
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    border-bottom: 1px solid $border-color;
}
